'use client';

import React from "react";
import { ParallaxProvider } from 'react-scroll-parallax';
import ParallaxScroll from "./ParallaxScroll";

const App = () => {
  return (
    <ParallaxProvider>
      <ParallaxScroll />
    </ParallaxProvider>
  );
};

export default App;
